@keyframes rotate {
    0% { transform: rotate(0deg); }
    25% { transform: rotate(90deg); }
    50% { transform: rotate(180deg); }
    75% { transform: rotate(270deg); }
    100% { transform: rotate(360deg); }
  }
  
  .rotate {
    @apply animate-rotate;
  }
  
  @tailwind base;
  @tailwind components;
  @tailwind utilities;
  
  @layer utilities {
    .animate-rotate {
      animation: rotate 0.5s ease-in-out;
    }
  }
  